body {
  color: #333333;
  font: normal 125%/1.4 "Roboto", sans-serif;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/*================================================================
  Buttons
  To create a button: add the base button class, a color
  class, a size class, and (when needed) a page-specific class.
  Index:
  - Base class
  - Color classes
  - Size classes
  - Page-specific classes
=================================================================*/
/*---------------------------
  Button Base class
  * Use on all buttons
---------------------------*/
.btn {
  border: 0;
  box-shadow: 0 0 8px 0 rgba(51, 51, 51, 0.2);
  display: inline-block;
  font-weight: 700;
  text-decoration: none;
  transition: all 100ms ease-in;
  white-space: nowrap;
  align-items: center;
  display: inline-flex;
  justify-content: center;
  margin: 5px 0;
}

.btn {
  align-items: center;
  display: inline-flex;
  justify-content: center;
  margin: 5px 0;
}

/*---------------------------
  Button Colors
---------------------------*/
/*   Primary Button  ---  Green   */
.btn {
  background: #247984;
  color: #ffffff;
  border: 4px solid transparent;
}
.btn:focus, .btn.focus {
  border-color: #0D5761;
  outline-color: #0D5761;
}
.btn:disabled {
  background: #c6c6c6;
}
.btn:hover, .btn:active, .btn.hover, .btn.active {
  background: #0D5761;
  color: #ffffff;
  text-decoration: none;
}

/*---------------------------
  Button Sizes
---------------------------*/
.btn-sm {
  height: 40px;
  padding: 0 32px;
  border-radius: 20px;
  font-size: 1rem;
}

.btn-md {
  height: 60px;
  padding: 0 50px;
  border-radius: 60px;
  font-size: 1.25rem;
}

.btn-lg {
  height: 72px;
  padding: 0 60px;
  border-radius: 100px;
  font-size: 1.5rem;
}
@media (max-width: 767px) {
  .btn-lg {
    height: 60px;
    padding: 0 50px;
    border-radius: 60px;
    font-size: 1.25rem;
  }
}

/*----------------------------------------------------
  Page-Specific Button Classes
  * Use when special styling is needed for a button
  on a specific page (usually placement)
-----------------------------------------------------*/
/*
- Page-specific class name formatting should be:
  btn--page-name
  EXAMPLE: btn--wins
- If there is more than one button in the page, formatting for each button should be:
  btn--page-name-button-function
  EXAMPLE: btn--home-getting-started
- Page-specific class names should live in that specific page's .scss file.
*/
table {
  background-color: #ffffff;
}
table col[class*=col-] {
  position: static;
  display: table-column;
  float: none;
}
table td[class*=col-],
table th[class*=col-] {
  position: static;
  display: table-cell;
  float: none;
}

caption {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  color: #989898;
  text-align: left;
}

th {
  text-align: left;
}

.table {
  width: 100%;
  max-width: 100%;
  margin-bottom: 1.5rem;
}
.table > thead > tr > th,
.table > thead > tr > td,
.table > tbody > tr > th,
.table > tbody > tr > td,
.table > tfoot > tr > th,
.table > tfoot > tr > td {
  padding: 0.5rem;
  line-height: 1.5;
  vertical-align: top;
  border-top: 1px solid #333333;
}
.table > thead > tr > th {
  vertical-align: bottom;
  border-bottom: 2px solid #333333;
}
.table > caption + thead > tr:first-child > th,
.table > caption + thead > tr:first-child > td,
.table > colgroup + thead > tr:first-child > th,
.table > colgroup + thead > tr:first-child > td,
.table > thead:first-child > tr:first-child > th,
.table > thead:first-child > tr:first-child > td {
  border-top: 0;
}
.table > tbody + tbody {
  border-top: 2px solid #333333;
}
.table .table {
  background-color: #ffffff;
}

.table-condensed > thead > tr > th,
.table-condensed > thead > tr > td,
.table-condensed > tbody > tr > th,
.table-condensed > tbody > tr > td,
.table-condensed > tfoot > tr > th,
.table-condensed > tfoot > tr > td {
  padding: 0.25rem;
}

.table-bordered {
  border: 1px solid #333333;
}
.table-bordered > thead > tr > th,
.table-bordered > thead > tr > td,
.table-bordered > tbody > tr > th,
.table-bordered > tbody > tr > td,
.table-bordered > tfoot > tr > th,
.table-bordered > tfoot > tr > td {
  border: 1px solid #333333;
}
.table-bordered > thead > tr > th,
.table-bordered > thead > tr > td {
  border-bottom-width: 2px;
}

.table-striped > tbody > tr:nth-of-type(odd) {
  background-color: #F8F9FC;
}

.table-hover > tbody > tr:hover {
  background-color: #989898;
}

.table-responsive {
  min-height: 0.01%;
  overflow-x: auto;
}
@media screen and (max-width: (max-width: 767px)) {
  .table-responsive {
    width: 100%;
    margin-bottom: 1.125rem;
    overflow-y: hidden;
    -ms-overflow-style: -ms-autohiding-scrollbar;
    border: 1px solid #F8F9FC;
  }
  .table-responsive > .table {
    margin-bottom: 0;
  }
  .table-responsive > .table > thead > tr > th,
  .table-responsive > .table > thead > tr > td,
  .table-responsive > .table > tbody > tr > th,
  .table-responsive > .table > tbody > tr > td,
  .table-responsive > .table > tfoot > tr > th,
  .table-responsive > .table > tfoot > tr > td {
    white-space: nowrap;
  }
  .table-responsive > .table-bordered {
    border: 0;
  }
  .table-responsive > .table-bordered > thead > tr > th:first-child,
  .table-responsive > .table-bordered > thead > tr > td:first-child,
  .table-responsive > .table-bordered > tbody > tr > th:first-child,
  .table-responsive > .table-bordered > tbody > tr > td:first-child,
  .table-responsive > .table-bordered > tfoot > tr > th:first-child,
  .table-responsive > .table-bordered > tfoot > tr > td:first-child {
    border-left: 0;
  }
  .table-responsive > .table-bordered > thead > tr > th:last-child,
  .table-responsive > .table-bordered > thead > tr > td:last-child,
  .table-responsive > .table-bordered > tbody > tr > th:last-child,
  .table-responsive > .table-bordered > tbody > tr > td:last-child,
  .table-responsive > .table-bordered > tfoot > tr > th:last-child,
  .table-responsive > .table-bordered > tfoot > tr > td:last-child {
    border-right: 0;
  }
  .table-responsive > .table-bordered > tbody > tr:last-child > th,
  .table-responsive > .table-bordered > tbody > tr:last-child > td,
  .table-responsive > .table-bordered > tfoot > tr:last-child > th,
  .table-responsive > .table-bordered > tfoot > tr:last-child > td {
    border-bottom: 0;
  }
}